<Card shadow="lg" radius="xl" className="w-full bg-gradient-to-r from-primary/20 via-white to-primary/10">
  <CardBody className="flex flex-col items-center justify-center gap-6 py-16 md:py-24 text-center">
    <div className="inline-block max-w-5xl">
      <span className={title({ color: "primary" })}>Protecting&nbsp;</span>
      <span className={title({ color: "violet" })}>what matters most&nbsp;</span>
      <br />
      <span className={title({ color: "primary" })}>
        with trusted insurance solutions tailored for you.
      </span>
      <div className={subtitle({ class: "mt-4 text-gray-600" })}>
        Comprehensive, reliable, and fast coverage for individuals, families, and businesses.
      </div>
    </div>

    {/* CTA Buttons */}
    <div className="flex gap-4 mt-8 flex-wrap justify-center">
      <Link
        href={siteConfig.links.getQuote || "#"}
        className="relative overflow-hidden px-6 py-3 rounded-full text-white font-semibold bg-primary transition-all duration-500 hover:scale-105"
      >
        <span className="relative z-10">Get a Quote</span>
      </Link>

      <Link
        href={siteConfig.links.services || "#"}
        className="relative overflow-hidden px-6 py-3 rounded-full text-white font-semibold bg-danger transition-all duration-500 hover:scale-105"
      >
        <span className="relative z-10">Our Services</span>
      </Link>
    </div>
  </CardBody>
</Card>